html {
  font-size: 62.5%;
}

body {
  color: #333;
  background-color: #f5f5f5;
  font-family: Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

ul {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

li {
  margin: 0 1rem;
}

a {
  color: #333;
  border: 2px solid #ccc;
  border-radius: 4px;
  padding: 1rem;
  font-size: 2.4rem;
  text-decoration: none;
  transition: all .3s;
}

a:hover {
  color: #fff;
  background-color: #333;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #b6b6b6;
  border: none;
  border-radius: 4px;
  margin: 1rem;
  padding: 1rem;
  font-size: 2.4rem;
  transition: all .3s;
}

button:hover {
  background-color: #4c4c4c;
}

